import React, { useState, useEffect } from "react";
import "./styles.css";
import TimePicker from "rc-time-picker";
import moment from "moment";
import "rc-time-picker/assets/index.css";

export default function App() {
  const defaultValue = moment().hour(0).minute(0);
  const [startTime, setStartTime] = useState(moment("12:OO PM", "hh:mm a"));
  const [endTime, setEndTime] = useState(moment("2:OO PM", "hh:mm a"));

  const handleStartChange = (e) => {
    const startTimeString = e.format("LT");
    setStartTime(startTimeString);
    const addEndTime = moment(startTime, "h:mm a")
      .add(2, "hours")
      .format("h:mm a");
    setEndTime(addEndTime);
  };
  console.log(startTime, "start");
  console.log(endTime, "end");
  const handleEndChange = (e) => {
    const endTimeString = e.format("LT");
    setEndTime(endTimeString);
  };

  return (
    <div className="App">
      <h1>Hello CodeSandbox</h1>
      <h2>Edit to see some magic happen!</h2>
      <label>start Time </label>
      <TimePicker
        placeholder="Select Time"
        use12Hours
        showSecond={false}
        focusOnOpen={true}
        format="hh:mm A"
        onChange={handleStartChange}
        defaultValue={startTime}
      />
      <br />
      <p>
        The End time value should automaticlly updated after selecting the time
        in Start time
      </p>
      <label>End Time </label>
      <TimePicker
        placeholder="Select Time"
        use12Hours
        showSecond={false}
        focusOnOpen={true}
        format="hh:mm A"
        onChange={handleEndChange}
        defaultValue={endTime}
        // value={endTime}
      />
      {/* <div>{startTime}</div>
      <div>{endTime}</div> */}
    </div>
  );
}
